.navtab {
  margin: 151px 0 30px;
  display: flex;
  column-gap: 10px;
}
.navtab__link {
  padding: 10px 0;
  width: 96px;
  text-align: center;
  border-radius: 10px;
  background: #303030;
  text-decoration: none;
  color: #fff;
  font-family: Inter, Arial, Helvetica, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
  transition: opacity 0.4s ease-in-out;
}
.navtab__link:hover {
  opacity: 0.7;
}
@media screen and (max-width: 768px) {
  .navtab {
    margin: 314px 0 30px;
  }
}
@media screen and (max-width: 450px) {
  .navtab {
    margin: 212px 0 17px;
    column-gap: 6px;
  }
  .navtab__link {
    width: 82px;
    padding: 5px 0;
    font-size: 10px;
    line-height: 16px;
  }
}
